const _ = require('underscore');
const AVError = require('./error');
const AVRequest = require('./request').request;

module.exports = function(AV) {
  /**
   *  LeanCloud
   *  <a href='/docs/leaninsight_guide.html'></a>
   * <p><strong><em>
   *   
   * </em></strong></p>
   * @namespace
   */
  AV.Insight = AV.Insight || {};

  _.extend(AV.Insight, /** @lends AV.Insight */ {

    /**
     *  Insight  Job id id 
     * AV.Insight.JobQuery 
     * @param {Object} jobConfig  JSON <code><pre>
     *                   { "sql" : "select count(*) as c,gender from _User group by gender",
     *                     "saveAs": {
     *                         "className" : "UserGender",
     *                         "limit": 1
     *                      }
     *                   }
     *                  </pre></code>
     *               sql  SQL  saveAs limit  1000
     * @param {AuthOptions} [options]
     * @return {Promise} A promise that will be resolved with the result
     * of the function.
     */
    startJob: function(jobConfig, options) {
      if(!jobConfig || !jobConfig.sql) {
        throw new Error('Please provide the sql to run the job.');
      }
      var data = {
        jobConfig: jobConfig,
        appId: AV.applicationId
      };
      var request = AVRequest("bigquery", 'jobs', null, 'POST',
                                   AV._encode(data, null, true), options);

      return request.then(function(resp) {
        return AV._decode(resp).id;
      });
    },

    /**
     *  Insight  end 
     *  <p><strong><em>
     *     
     *  </em></strong></p>
     * @param {String} event  'end' 
     * @param {Function}  (err, id) err 
     *                   id  id id AV.Insight.JobQuery 
     *
     */
    on: function(event, cb) {
    }
  });

  /**
   *  Insight 
   * @class
   * @param {String} id  id
   * @since 0.5.5
   */
  AV.Insight.JobQuery = function(id, className) {
    if(!id) {
      throw new Error('Please provide the job id.');
    }
    this.id = id;
    this.className = className;
    this._skip = 0;
    this._limit = 100;
  };

  AV.Insight.JobQuery.prototype = {

    /**
     * Sets the number of results to skip before returning any results.
     * This is useful for pagination.
     * Default is to skip zero results.
     * @param {Number} n the number of results to skip.
     * @return {AV.Query} Returns the query, so you can chain this call.
     */
    skip: function(n) {
      this._skip = n;
      return this;
    },

    /**
     * Sets the limit of the number of results to return. The default limit is
     * 100, with a maximum of 1000 results being returned at a time.
     * @param {Number} n the number of results to limit to.
     * @return {AV.Query} Returns the query, so you can chain this call.
     */
    limit: function(n) {
      this._limit = n;
      return this;
    },

    /**
     *  JSON  status  totalCount 
     * results previewCount 
     * startTimeendTime 
     *
     * @param {AuthOptions} [options]
     * @return {Promise} A promise that will be resolved with the result
     * of the function.
     *
     */
    find: function(options) {
      var params = {
        skip: this._skip,
        limit: this._limit
      };

      var request = AVRequest("bigquery", 'jobs', this.id, "GET",
                                   params, options);
      var self = this;
      return request.then(function(response) {
        if(response.error) {
          return AV.Promise.reject(new AVError(response.code, response.error));
        }
        return AV.Promise.resolve(response);
      });
    }

  };

};



